import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { DataService } from 'src/app/Services/provider.service';
import { Router } from '@angular/router';
declare var $: any
@Component({
  selector: 'app-create-vendor',
  templateUrl: './create-vendor.component.html',
  styleUrls: ['./create-vendor.component.css']
})
export class CreateVendorComponent implements OnInit {
  createVendorForm: any;
  image: string | ArrayBuffer;
  url: string | ArrayBuffer;
  @ViewChild('address') address: ElementRef;
  responseData: Object;
  codeArray: any;

  constructor(public formBuilder: FormBuilder, private service: DataService, private route: Router) {

    this.createVendorForm = formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      phone: ['', [Validators.required, Validators.pattern('[6-9]\\d{9}')]],
      address: ['', Validators.required],
      profilePic: ['', Validators.required],
      storeType: ['', Validators.required],
      prefix: ['', Validators.required],
    })
  }

  ngOnInit() {
    this.getCountryCodesList()
    //Custom Select Option Start Here
    $('.sales_year_choose select.select').each(function () {
      
      var title = $(this).attr('title');
      if ($('option:selected', this).val() != '') title = $('option:selected', this).text();
      $(this)
        .css({ 'z-index': 10, 'opacity': 0, '-khtml-appearance': 'none' })
        .after('<span class="select">' + title + '</span>')
        .change(function () {
          this.val = $('option:selected', this).text();
          $(this).next().text(this.val);
        });
    });
    //End

  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  changeListener($event): void {
    console.log($event)
    this.readThis($event.target);
    this.readUrl($event.target);
  }

  readThis(inputValue: any): void {
    var file: File = inputValue.files[0];
    var myReader: FileReader = new FileReader();

    myReader.onloadend = (e) => {
      this.image = myReader.result;
    }
    myReader.readAsDataURL(file);
  }

  readUrl(inputValue: any) {
    if (inputValue.files && inputValue.files[0]) {
      var reader: FileReader = new FileReader();

      reader.onload = (event: ProgressEvent) => {
        this.url = reader.result;
      }
      console.log(this.url)
      reader.readAsDataURL(inputValue.files[0]);
    }
  }


  createVendor(val) {

    console.log(this.address.nativeElement.value)

    console.log('console', val)
    // return
    this.service.getLatApi(val.address).subscribe(response1 => {
      this.responseData = response1;
      var latitude = this.responseData['results'][0]['geometry']["location"]['lat']
      var longitude = this.responseData['results'][0]['geometry']["location"]['lng']
      let sentData = {
        firstName: val.firstName,
        lastName: val.lastName,
        email: val.email,
        phone: val.phone,
        address: this.address.nativeElement.value,
        profilePic: this.image,
        storeType: val.storeType,
        lat: latitude,
        countryCode: val.prefix,
        lng: longitude,
        lang: 'en',
        status: 'inactive'
      }
      // })
      // console.log("2222",sentData)
      // return
      // console.log("WWWWWWWW", this.sentData)
      this.service.postApi('admin/createVendor', sentData, 0).subscribe(response => {
        this.service.spinnerShow()
        this.responseData = response;
        console.log(response);
        if (this.responseData['statusCode'] == 200) {
          this.service.showSuccess(response['statusMessage'])
          this.service.spinnerHide()
          this.route.navigate(['/login'])
        }
        else if (this.responseData['statusCode'] == 400 || this.responseData['statusCode'] == 401) {
          // this.route.navigate(['/createvendor']);
          this.service.showError(response['statusMessage'])
        }
      }, error => {
        console.log('error occur', error)
        this.service.showBug("")
        // this.service.showError('Server Error')
      })
    })
  }







  //
  // onSelectCountry() {
  //   console.log('selected country => ' + this.createVendorForm.controls['country'].value)
  //   let selectedCountry = this.createVendorForm.controls['country'].value
  //   this.statesArr = selectedCountry ?  this.codeArray.filter(x => x.country == selectedCountry)[0].states : []
  //   console.log('selected country => ' , this.merchantForm.value)

  // }

  getCountryCodesList() {
    this.service.getCountryCodeJson().subscribe(response => {
      this.codeArray = response['countries'];
    },
      error => {
        console.log(`Error is ${JSON.stringify(error)}`)
      })
  }

  reset() {
    this.createVendorForm.reset()
  }
  Cancel() {
    this.createVendorForm.reset()
    this.route.navigate(['/login']);
  }
}
