import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/Services/provider.service';
import { DatepickerOptions } from 'ng2-datepicker';
import { IMyDpOptions, IMyDateModel } from 'mydatepicker';
import { FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-create-offer',
  templateUrl: './create-offer.component.html',
  styleUrls: ['./create-offer.component.css']
})
export class CreateOfferComponent implements OnInit {


  ckeditorContent: any




  dropdownSettings = {
    singleSelection: false,
    idField: '_id',
    textField: 'product',
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    itemsShowLimit: 100,
    allowSearchFilter: true
  };









  public myDatePickerOptions: IMyDpOptions = {
    dateFormat: 'yyyy-mm-dd',
    inline: false,
    showInputField: true,
    showTodayBtn: false,
  };
  public myDatePickerOptions2: IMyDpOptions = {
    dateFormat: 'yyyy-mm-dd',
    inline: false,
    showInputField: true,
    showTodayBtn: false,
  };
  Form: any;
  productList: any;

  imageData: any = [];
  constructor(private service: DataService, private formBuilder: FormBuilder) {
    this.getproductList()
    this.Form = this.formBuilder.group({
      title: ['', Validators.required],
      description: ['', Validators.required],
      StartDate: ['', Validators.required],
      EndDate: ['', Validators.required],
      offerType: ['', Validators.required],
      file: ['', Validators.required],
      image: ['', Validators.required],
      value: ['', Validators.required],
      applicableType: ['', Validators.required],
      applicableOn: ['', Validators.required],
      minimumRequirement: ['', Validators.required],
    })
  }

  ngOnInit() {
    this.onDateChange()
    this.service.setTitle('Create Offer')
  }


  onDateChanged(event: IMyDateModel) {
    // console.log(`event${JSON.stringify(event)}`)
    this.Form.value.StartDate
    let endDate = new Date(event.formatted)
    let copy2 = this.getCopyOfOptions();
    copy2.disableUntil = {
      year: endDate.getFullYear(),
      month: endDate.getMonth() + 1,
      day: endDate.getDate()
    };
    this.myDatePickerOptions2 = copy2
  }
  onDateChange() {
    let startD = new Date()
    let copy1 = this.getCopyOfOptions();
    copy1.disableUntil = {
      year: startD.getFullYear(),
      month: startD.getMonth() + 1,
      day: startD.getDate()
    };
    this.myDatePickerOptions = copy1

  }
  onDateChanged2(event: IMyDateModel) {

  }
  getCopyOfOptions(): IMyDpOptions {
    return JSON.parse(JSON.stringify(this.myDatePickerOptions));
  }

  getproductList() {

    this.service.getApi('admin/getProductList?lang=en', 1).subscribe(response => {
      // console.log("response-======>>>",response)
      if (response['statusCode'] == 200) {

        console.log(response)
        this.productList = response['result']
        this.dropdownSettings = {
          singleSelection: false,
          idField: '_id',
          textField: 'product',
          selectAllText: 'Select All',
          unSelectAllText: 'UnSelect All',
          itemsShowLimit: 100,
          allowSearchFilter: true
        };
      }
      else {

        this.service.showError('NOT FOUND')
      }
    }, error => {
      console.log('error occur', error)
      this.service.showError('Server Error')
    })

  }

  applicable(val) {
    console.log('variable', val)

    if (val == 0) {

      this.service.getApi('vendor/getVendorProductCategorylist?lang=en', 1).subscribe(response => {
        // console.log("response-======>>>",response)
        if (response['statusCode'] == 200) {

          console.log(response)
          this.productList = response['result']
          this.dropdownSettings = {
            singleSelection: false,
            idField: '_id',
            textField: 'productcategoryName',
            selectAllText: 'Select All',
            unSelectAllText: 'UnSelect All',
            itemsShowLimit: 100,
            allowSearchFilter: true
          };
        }
        else {

          this.service.showError('NOT FOUND')
        }
      }, error => {
        console.log('error occur', error)
        this.service.showError('Server Error')
      })



    }
    else {
      this.getproductList()
    }
  }

  onFileChanged(evt: any) {
    const file = evt.target.files[0];

    if (file) {
      console.log('2222', file);
      const reader = new FileReader();
      reader.onload = this.handleReaderLoaded.bind(this);
      reader.readAsBinaryString(file);
      // console.log(this.base64textString)
    }
  }
  handleReaderLoaded(e) {
    this.imageData.push('data:image/png;base64,' + btoa(e.target.result));
  }
  //save Offer
  saveOffer(val) {
    let a = []
    for (let i in val.applicableOn) {
      a.push(val.applicableOn[i]._id)
    }
    let temp = {
      "userId": localStorage.getItem('token'),
      "offerName": val.title,
      "description": val.description,
      "offerImage": this.imageData,
      "offerType": val.offerType,
      "value": val.value,
      "applicableType": val.applicableType,
      "applicableOn": a,
      "startDate": new Date(val.StartDate.jsdate).toISOString(),
      "endDate": new Date(val.EndDate.jsdate).toISOString(),
      "lang": "en"
    }
    this.service.postApi('vendor/addVendoroffer', temp, 1).subscribe(response => {
      this.service.spinnerShow()

      console.log("response-======>>>", response)
      if (response['statusCode'] == 200) {
        this.service.showSuccess('Offer Added')
        this.service.spinnerHide()
        this.Form.reset()
      }
      else {
        this.Form.reset()
        this.service.showError('SOMETHING WENT TO WRONG')
      }
    }, error => {
      console.log('error occur', error)
      this.service.showError('Server Error')
    })


  }
}
